import React, { memo } from "react";
import ImageType from "../../../CommonComponents/MediaType/ImageType";
import GridHeader from "./GridHeader";
import GridFooter from "./GridFooter";

const Grid = ({ size, data, spaceVal }) => {
  const Gridbox = () => (
    <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
  );

  const GridboxInside = () => (
    <>
      <div className="w-50 rounded-4 h-8px mb-4"></div>
      <div
        className="w-100 h-26px mb-1 bg-gray-200"
        style={{ maxWidth: 90 }}
      ></div>
    </>
  );

  const GridColumn = ({ col }) => {
    return [1, 2].map((key) => (
      <div
        key={key}
        className={col ? (key === 0 ? `pb-5` : `pb-3`) : `row gx-2 mb-3`}
      >
        <div className="col">
          <div className="border p-3">
            <Gridbox />
            <Gridbox />
            <Gridbox />
            <Gridbox />
            <GridboxInside />
          </div>
        </div>
      </div>
    ));
  };

  const Getgrid = memo(({ row, column }) => {
    let count = -1;
    return (
      <>
        {Array.from({ length: row }).map((_, gridKey) => (
          <div
            key={gridKey}
            className={[3, 4].includes(size) ? `row gx-1` : `row gx-0`}
          >
            {Array.from({ length: column }).map((_, index) => {
              count++;
              const url =
                data[count]?.ugc_products?.length > 0
                  ? data[count].ugc_products[0].UgcProduct.product_url
                  : data[count]?.link || "";

              const mediaContent = (
                <ImageType
                  mediaClass="bg-gray-200"
                  size={100}
                  height={300}
                  width={300}
                  url={data[count]?.postFile || ""}
                  isCover={true}
                  alt=""
                />
              );

              return url ? (
                <a
                  data-id={`media-${data[count]?.id}`}
                  href={url}
                  target="_blank"
                  rel="noopener noreferrer"
                  key={index}
                  className="col media_col"
                  title="img"
                  style={{ padding: spaceVal / 2 }}
                >
                  {mediaContent}
                </a>
              ) : (
                <span
                  key={index}
                  className="col media_col"
                  style={{ padding: spaceVal / 2 }}
                >
                  {mediaContent}
                </span>
              );
            })}
          </div>
        ))}
      </>
    );
  });

  return (
    <div className="emaiPreviewInner">
      <GridHeader />
      <div>
        {size === 1 ? (
          <div className="row gx-3">
            <div className="w-75 mx-auto">
              <div className="p-1">
                <Getgrid row={2} column={2} />
              </div>
            </div>
          </div>
        ) : size === 2 ? (
          <div className="row gx-3">
            <div className="col-5">
              <GridColumn col={1} />
            </div>
            <div className="col-7">
              <div className="mb-3 rounded-1 w-75 mx-auto">
                <div className="p-1">
                  <Getgrid row={3} column={2} />
                </div>
              </div>
            </div>
          </div>
        ) : size === 3 ? (
          <div className="mb-3 rounded-1 w-75 mx-auto">
            <div className="p-1">
              <Getgrid row={3} column={3} />
            </div>
          </div>
        ) : (
          <>
            <GridColumn />
            <div className="mb-3 rounded-1 w-75 mx-auto">
              <div className="p-1">
                <Getgrid row={1} column={4} />
              </div>
            </div>
            <div className="f-center flex-column w-100 px-4 mb-3">
              <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
              <div className="w-75 rounded-4 h-8px mb-2"></div>
            </div>
          </>
        )}
      </div>
      <GridFooter />
    </div>
  );
};

export default Grid;
